$(function(){

  // Sticky Call
  //   
  $(window).scroll(function(){
    var scrollPos = $(window).scrollTop();
    // console.log(scrollPos);

    if(scrollPos>20){
      $('#navi').addClass('fixed');
    }else {
      $('#navi').removeClass('fixed');
    }
    
  });

  //    
  var menuItem = $('#gnb li:nth-child(-n+6) a, #navi h1 a');

  menuItem.click(function(){
    var el = $(this).attr('href');  //ex) #about
    // console.log(el);
    var elWrap = $(el);           //ex) $('#about')
    // console.log(elWrap);
    
    scrollMove(elWrap, 50);
  });

  //    
  function scrollMove(elWrap, navHeight){
    var offset = elWrap.offset().top; //   
    var totalScroll = offset - navHeight;

    $('html, body').animate({scrollTop: totalScroll}, 800);
  }

  // Scrollspy Call
  $('body').scrollspy({ 
    offset: 51  
  });

  //  
  var blankMenu = $('#gnb li:nth-child(7) a, #gnb li:nth-child(8) a');
  blankMenu.click(function(){
    event.preventDefault();
  });

  
  // jarallax Call
  $('.jarallax').jarallax({
    speed: 0.2,
    videoPlayOnlyVisible: false,
    videoLazyLoading: false
  });

  // Bxslider Call
  $('.bxslider').bxSlider({
    minSlides: 5,
    maxSlides: 5,
    auto: true,
    pager: true,
    controls: false,
    slideWidth: 210,
    slideMargin: 10,
    moveSlides: 1
  });


  // Magnific Popup Call
  var gallery = $('#work .col-lg-6>div');
  gallery.magnificPopup({
    delegate: 'a', 
    type: 'image',
    gallery: {
      enabled: true
    }
  });


  // ScrollMagic Call
  var controller = new ScrollMagic.Controller();

  //  (Scene) 
  var c1 = $('.circle1'),
       c2 = $('.circle2'),
       tl1 = new TimelineMax();

  //  : 
  // jQuery => $('').animate({CSS }, (duration), , );
  // GSAP  => tl1.from('', (duration), {CSS });
  tl1.from(c1, 1, {autoAlpha: 0, y:30}, 0.1)
    .from(c2, 1, {autoAlpha: 0, y:30}, 0.7);

  var scene1 = new ScrollMagic.Scene({
    triggerElement: '#analysis',  
    triggerHook: "0.3"
  })
  .setTween(tl1)
  .addTo(controller)
  .on('start end', function(){

    // Circle1 Setting
    $('.circle1').circleProgress({
      value: 0.85,
      size: 126
    }).on('circle-animation-progress', function(event, progress) {
      $(this).find('strong').html(Math.round(85 * progress) + '<i>%</i>');
    });

    // Circle2 Setting
    $('.circle2').circleProgress({
      value: 0.75,
      size: 126
    }).on('circle-animation-progress', function(event, progress) {
      $(this).find('strong').html(Math.round(75 * progress) + '<i>%</i>');
    });

  }); 


  //  (Scene) 
  var pb1 = $('.progress-bar1'),
       pb2 = $('.progress-bar2'),
       pb3 = $('.progress-bar3'),
       pb4 = $('.progress-bar4'),
       tl2 = new TimelineMax();

  tl2.fromTo(pb1, 1, {width: 0}, {width: "25%"}, 0.1)
    .fromTo(pb2, 1, {width: 0}, {width: "50%"}, 0.5)
    .fromTo(pb3, 1, {width: 0}, {width: "75%"}, 1)
    .fromTo(pb4, 1, {width: 0}, {width: "85%"}, 1.5);

  var scene2 = new ScrollMagic.Scene({
    triggerElement: '#plan',  
    triggerHook: "0.3"
  })
  .setTween(tl2)
  .addTo(controller);



  // Mobile Navigation Toggle
  var mobileMenu = $('.navbar-fixed-top #gnb');

  mobileMenu.mouseleave(function(){
    $(this).animate({'height':0}, 800, function(){
       $(this).removeClass('in');
     });
  });




});